import React from 'react';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { Header } from 'components';
import { Layout, Container } from 'layouts';
import styled from '@emotion/styled';
import $ from 'jquery';

const divStyle = {
  margin: "1em 1em 1em 0",
  width: "100%",
  "textAlign": "left"
};

const FormDiv = styled.div` 
  @media (max-width: 650px)
  {
    width: 50%;
  }

  display: inline-block;
  width: 20%;
`;

const Disclaimer = styled.div` 
  font-size: .7rem;
  font-style: italic;
`;

const Button = styled.button `
  width: 14em;
  color: white;
  border: none;
  padding: 5px;
  &:hover {
    cursor: pointer;
    background-color: #0066ffe0;
    transition: ease-in-out .25s;
  }
  background-color: #002bffc9;
`;

const messageStyle = {
  display: 'none',
  backgroundColor: '#00e365b8',
};

const badMessageStyle = {
  display: 'none',
  backgroundColor: 'rgba(227, 0, 0, 0.72)',
};


class OrderForm extends React.Component { 
  constructor(props) {
    super(props);
    let price = props.price.slice(1);
    this.state = {
      weapon: props.weapon,
      originalPrice: price,
      currentPrice: props.price,
      isCarryAvailable: props.isCarryAvailable,
      serviceType: 'recov'
    }

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    let val = $('[name="serviceType"]:checked')[0].value;
    let price = `$${(val == 'recov' ? this.state.originalPrice : Number(this.state.originalPrice) * 1.75)}`;
    this.setState({ serviceType: val, currentPrice: price });
  }

  render() {
    return (
      <div>
        <hr />
        <h3>Please fill out the form below to place your order.</h3>
          <div style={divStyle}>
            <FormDiv>
              <label htmlFor="pinnacleItem">Product: </label>
            </FormDiv>
            <FormDiv id="pinnacleItem">{this.state.weapon}</FormDiv>
          </div>
            { this.state.isCarryAvailable == true &&
              <div style={divStyle}>
                <FormDiv>
                  <label htmlFor="serviceType">Service Type: </label>
                </FormDiv>
                <FormDiv id="serviceType">
                  <input type="radio" name="serviceType" value="recov" onChange={this.handleChange} checked={this.state.serviceType == 'recov'} />Recovery (we play for you)<br />
                  <input type="radio" name="serviceType" value="carry" onChange={this.handleChange} />Carry (you play with us)
                </FormDiv>
              </div>
            }
          <div style={divStyle}>
            <FormDiv>
              <label htmlFor="price">Price: </label>
            </FormDiv>
            <FormDiv id="price">{this.state.currentPrice}</FormDiv>
            <Disclaimer>Keep in mind the price listed is for a full service (usually 0-2100 plus all quest steps). If you need partial service, please specify in the notes below and we will negotiate a lower rate.</Disclaimer>
          </div>
          <div style={divStyle}>
            <FormDiv>
              <label htmlFor="platform">Platform: </label>
            </FormDiv>
            <FormDiv id="platform">PC</FormDiv>
            <Disclaimer>We currently only offer our services on PC. If you need a service on console, please fill out the fill and let us know in the comments and we will find someone trustworthy to perform your service.</Disclaimer>
          </div>
          <div style={divStyle}>
            <FormDiv>
              <label htmlFor="discord">Discord ID: </label>
            </FormDiv>
            <FormDiv>
              <input id="discord" type="text" required />
            </FormDiv>
          </div>
          <div style={divStyle}>
            <FormDiv>
              <label htmlFor="email">Email: </label>
            </FormDiv>
            <FormDiv>
              <input id="email" type="text" required />
            </FormDiv>
          </div>
          <div style={divStyle}>
            <FormDiv>
              <label htmlFor="notes">Notes or questions about the order: </label>
            </FormDiv>
            <FormDiv>
              <textarea id="notes" rows="2" cols="50" required />
            </FormDiv>
          </div>
          <div style={divStyle}>
            <Button onClick={PlaceOrder}>Place Order</Button>
          </div>
          <div id="message" style={messageStyle}></div>
          <div id="badMessage" style={badMessageStyle}></div>
        <Helmet>
          <script src="smtp.js"></script>
        </Helmet>
      </div>
    );
  }
}

function PlaceOrder(e) {
  e.preventDefault();
  let product = $('#pinnacleItem')[0].innerText;
  let discord = $('#discord').val();
  let email = $('#email').val();
  let price = $('#price')[0].innerText;
  let notes = $('#notes').val();
  let serviceType = $('[name="serviceType"]:checked')[0].value;
  let body = `
    Product: ${product}<br>
    Discord: ${discord}<br>
    Price: ${price}<br>
    Service: ${serviceType}<br>
    Email: ${email}<br>
    Notes: ${notes}<br>
  `;

  if (discord == '' || discord.indexOf('#') == -1)
  {
    $('#badMessage').text('Please fill in your discord id correctly. It should contain a # symbol.').show();
    return;
  }

  if (email == '' || email.indexOf('@') == -1)
  {
    $('#badMessage').text('Please fill in your email correctly.').show();
    return;
  }

  $('#badMessage').text('').hide();
  Email.send({
   SecureToken: "d94d2b7d-8e70-417e-9ead-99aa42692de6",
    To : 'destinypinnacleservices@gmail.com',
    Subject : "Order placed for " + product,
    From: "destinypinnacleservices@gmail.com",
    Body: body
  }).then(
    message => {
      console.log('message', message);
      $('#message').text('Order sent successfully! We will contact you shortly.');
      $('#message').show();
    }
  );
}

export default OrderForm;

Container.propTypes = {
  center: PropTypes.object,
};
